.cart-container{
  position: fixed;
  z-index: 200;
  background-color: rgba(255,255,255, 0.7);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;

  .cart-details{
    width: 800px;
    background-color: white;
    filter: drop-shadow(5px 5px 10px rgba(0, 0, 0, 0.3));
    padding: 10px;
    border-radius: $default-radius;
    display: flex;
    flex-direction: column;

    .price, .taxes, button, .checkout-button{
      align-self: flex-end;
    }

    .price{
      margin-bottom: 10px;
    }

    button{
      margin-top: 30px;
    }
  }
}

.cart-item-container{
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 121px 350px 127px auto 22px;
  align-items: center;

  i{
    cursor: pointer;
  }

  .quantity-container{
    display: flex;
    align-items: center;
    font-size: 20px;

    i{
      margin: 5px;
      font-size: 20px;
      cursor: pointer;
    }
  }

  img{
    width: 100%;
  }
}